import React from 'react';
import { Link } from 'react-router-dom';
import ProfileImage from "../components/assets/ProfilePhoto.png";
import "./Header.scss";

const Header = () => {
  return (
    <div style={ {
        display: "flex",
        justifyContent: "space-between",
        color: "white",
        alignItems: "center",
        width:"92%",
        margin:"20px"
              } }>
        <p style={ { fontSize: "25px", fontWeight: 600 } }>Logo</p>
        <div className='headingOptions'>
        <Link to="/">
          <p className={ 'landingPageDialog' }>Home</p>
          </Link>
          <Link to="/Lottery">
          <p className={ 'landingPageDialog' }>Lottery</p>
          </Link>
          <Link to="/Minting">
          <p className={ 'landingPageDialog' }>Minting</p>
          </Link>
          <Link to="/Staking">
          <p className={ 'landingPageDialog' }>Staking</p>
          </Link>
          <img
            style={ { height: "44px", width: "44px", borderRadius: "22px", border: "1px solid white" } }
            src={ ProfileImage } alt="Profile Photo"/>
        </div>
      </div>
  )
}

export default Header
